.about {
    width: 100%;
    min-height: 100vh;
    background: red;
    padding-top: 15vh;
    padding-bottom: 3rem;
}

.about-product {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.about-pastil {
    height: 250px;
    width: 600px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    color: white;
}

.about-pastil h1 {
    align-self: flex-start;
    color: greenyellow;
}

.about-pastil p{
    font-size: 0.8rem;
}

.product-image img {
    width: 250px;
}

.marketing-label {
    padding: 2rem 5rem;
}

.marketing-boxes {
    padding: 0 5rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 2rem;
    flex-wrap: wrap;
}

.marketing-box {
    height: 250px;
    width: 400px;
    background: yellow;
}

.marketing-box img {
    height: 250px;
    width: 400px;
}

@media (max-width: 1200px) {
    .product-image img {
    width: 200px;
}
    .about-pastil h1 {
    font-size: 1.5rem;
}

.about-pastil p{
    font-size: 0.7rem;
}
}

@media (max-width: 1050px) {
    .product-image {
        display: none;
    }
    .about-product {
        padding: 0 5rem;
    }
     .about-pastil h1 {
    font-size: 1.5rem;
}

.about-pastil p{
    font-size: 0.8rem;
}
.about-pastil {
    width: 600px;
}

}