.nav {
    height: 15vh;
    width: 100%;
    padding: 1rem 5rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: fixed;
    top: 0;
    z-index: 3;
    backdrop-filter: blur(10px);
}

.contacts {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 300px;
}

.logo-container img{
    width: 100px;
}

.navigation-list {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 300px;
}

.icon {
    font-size: 1.3rem;
}

@media (max-width: 1050px) {
    .contacts {
        display: none;
    }
    .nav {
        flex-direction: row-reverse;
    }
}

@media (max-width: 750px) {
    .nav {
        flex-direction: row;
        padding: 2rem;
    }
    .navigation-list {
        display: none;
    }
}